<script>
import navItem from './nav-item'
import ahttp from "@/services/ahttp";
import api from "@/services/api";

export default {
  name: "navbar",
  data() {
    return {
      search: null,
      show: false,
      data: null,
      showModal: false,
    }
  },
  components: {
    navItem,
  },
  props: {
    bgColor: String
  },
  watch:{
  },
  computed: {
    getUser() {
      return this.$store.state.auth.user;
    },
  },
  methods: {
    async searchMulti() {
      let {data} = await ahttp.get(`/search/multi?${api}&query=${this.search}`);
      this.data = data.results;
    },
    movieStatus(vote) {
      let str = 'p-1 rounded text-gray-100'
      if (vote >= 8.0) return str+' bg-green-600'
      if (vote >= 6.0 && vote < 8.0) return str+ ' bg-yellow-600'
      if (vote >= 5.0 && vote < 6.0) return str+' bg-gray-600'
      if (vote < 5.0) return str+' bg-red-600'
    },
    open() {
      this.$emit('select-modal')
    }
  }
}
</script>

